import axios from "axios";
import {
  DefaultReadonlyProvider,
  getToken,
  getTokenFromAddress,
  networkIds,
} from "config/networks";
import { useConnectedWeb3Context } from "contexts";
import { BigNumber } from "ethers";
import { parseEther } from "ethers/lib/utils";
import { useEffect, useState } from "react";
import { ERC20Service } from "services/erc20";
import { ONE_ETHER, ZERO } from "utils/number";

interface IState {
  lpPrice: BigNumber;
  token0: BigNumber;
  token1: BigNumber;
  poolUsd: BigNumber;
}

export const useLPTokenPrice = (address: string) => {
  const [state, setState] = useState<IState>({
    lpPrice: ZERO,
    token0: ZERO,
    token1: ZERO,
    poolUsd: ZERO,
  });
  const { library: provider, networkId } = useConnectedWeb3Context();

  useEffect(() => {
    const calculatePrice = async () => {
      if (networkId === networkIds.HARMONY) {
        if (!provider) {
          return;
        }
        const lpToken = getTokenFromAddress(address, networkId);
        const lp = new ERC20Service(provider, "", lpToken.address);
        const woneToken = getToken("wone", networkId);
        const wone = new ERC20Service(provider, "", woneToken.address);
        const token0 = new ERC20Service(provider, "", lpToken.token0 || "");
        try {
          const response = (
            await axios.get(
              `https://api.coingecko.com/api/v3/simple/price?ids=harmony&vs_currencies=usd`
            )
          ).data;
          const wonePrice = parseEther(String(response.harmony.usd));

          const [totalSupply, woneBalance, token0Balance] = await Promise.all([
            lp.totalSupply(),
            wone.getBalanceOf(lpToken.address),
            token0.getBalanceOf(lpToken.address),
          ]);

          const lpPrice = woneBalance.mul(wonePrice).mul(2).div(totalSupply);
          const token0Price = woneBalance.mul(wonePrice).div(token0Balance);
          setState(() => ({
            lpPrice,
            token0: token0Price,
            token1: wonePrice,
            poolUsd: woneBalance.mul(wonePrice).mul(2).div(ONE_ETHER),
          }));
        } catch (error) {
          setState(() => ({
            lpPrice: ZERO,
            token0: ZERO,
            token1: ZERO,
            poolUsd: ZERO,
          }));
        }
      } else {
        const lpToken = getTokenFromAddress(address, networkIds.BSC);
        const lp = new ERC20Service(
          DefaultReadonlyProvider,
          "",
          lpToken.address
        );
        const busdToken = getToken("busd", networkIds.BSC);
        const busd = new ERC20Service(
          DefaultReadonlyProvider,
          "",
          busdToken.address
        );
        const token0 = new ERC20Service(
          DefaultReadonlyProvider,
          "",
          lpToken.token0 || ""
        );
        try {
          const [totalSupply, busdBalance, token0Balance] = await Promise.all([
            lp.totalSupply(),
            busd.getBalanceOf(lpToken.address),
            token0.getBalanceOf(lpToken.address),
          ]);

          const lpPrice = busdBalance.mul(2).mul(ONE_ETHER).div(totalSupply);
          const token0Price = busdBalance.mul(ONE_ETHER).div(token0Balance);
          setState(() => ({
            lpPrice,
            token0: token0Price,
            token1: ONE_ETHER,
            poolUsd: busdBalance.mul(2),
          }));
        } catch (error) {
          setState(() => ({
            lpPrice: ZERO,
            token0: ZERO,
            token1: ZERO,
            poolUsd: ZERO,
          }));
        }
      }
    };

    calculatePrice();

    let interval = setInterval(calculatePrice, 30000);

    return () => {
      clearInterval(interval);
    };
  }, [address]);

  return state;
};
