import axios from "axios";
import { getGraphInfo } from "config/networks";
import { useConnectedWeb3Context } from "contexts";
import { BigNumber } from "ethers";
import { useEffect, useState } from "react";
import { IIDODetails, IRoundData } from "types";
import { fetchQuery } from "utils/thegraph";
import IDOv4Abi from "abis/idov4.json";
import IDOSecAbi from "abis/idoSec.json";
import { useServices } from "helpers/useServices";

const query = `
  query ($id: ID!) {
    ido(id: $id) {
      id
      version
    }
  }
`;

interface IState {
  loading: boolean;
  data?: IIDODetails;
}

export const useIDO = (id: string) => {
  const { networkId } = useConnectedWeb3Context();
  const idoGraphHttpEndPoint = getGraphInfo("ido", networkId).httpUri;
  const [state, setState] = useState<IState>({ loading: true });
  const { multicall } = useServices();

  const loadIDO = async () => {
    setState((prev) => ({ ...prev, loading: true }));
    if (id === '0xcEade12851e9Dcc530ED2314E5E4677645e9b3e7') {
      let legio_claim_ido = {'id':'0xcEade12851e9Dcc530ED2314E5E4677645e9b3e7', 'version':'v0'}
      let creator = '0xFD723461878abFeeA82299B9700a64e4E17B76FF'
      let saleToken = '0x44c663476ecb54487c95e82c5bd05b9694253a9f'
      let saleTarget = BigNumber.from(261000.0)
      let fundToken = '0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56'
      let fundTarget = BigNumber.from(261000.0)
      let fundRaised = BigNumber.from(0)
      let totalReleased = BigNumber.from(0)
      let baseAmount = BigNumber.from(1)
      let minFundAmount = BigNumber.from(1)
      let meta = undefined
      let totalFunders = 0
      let roundData: IRoundData[] = [];
      const roundCalls = [1, 2, 3, 4, 5, 6].map((roundNumber) => ({
        name: "roundsFundRaised",
        address: id,
        params: [roundNumber],
      }));
      const roundResponse = await multicall.multicallv2(
        IDOv4Abi,
        roundCalls,
        {
          requireSuccess: false,
        }
      );

      roundData = roundResponse.map((info: BigNumber[], index: number) =>
              info && info.length > 0
                ? ({
                    roundId: BigNumber.from(index + 1),
                    fundRaised: info[0],
                    saleRaised: info[0].mul(saleTarget).div(fundTarget),
                  } as IRoundData)
                : null
      );
      // handle metadata to let IDOClaimRow can call renderContent
      let website = 'https://www.legiodao.com/'
      let telegram = 'https://t.me/legiodaoofficial'
      let twitter = 'https://twitter.com/LegioDAO'
      let discord = ''
      let description = 'G&Ls Angels and Demons is a trading NFT Play to eogical collectibles PLAY, COLLECT, TRADE AND RISE'
      let logo = "https://assets.coingecko.com/coins/images/25422/small/LegioDAO_transparent.png?1651736525"
      let title = "Legio"
      let tokenDecimals = 18
      let tokenSymbol = "$LGO"
      let banner = ''
      let images = ['aaa']
      const metadata = {
          website, 
          telegram, 
          twitter, 
          discord, 
          description,
          logo,
          title,
          tokenDecimals,
          tokenSymbol,
          banner,
          images
      }
      
      const ido = {
        ...legio_claim_ido,
        address: id,
        creator,
        startTime: 1656777600,
        endTime: 1656864000,
        claimTime: 1656950400,
        saleToken,
        saleTarget,
        fundToken,
        fundTarget,
        fundRaised,
        totalReleased,
        baseAmount,
        minFundAmount,
        meta,
        cliffTime: 1656950400,
        distributePercentAtClaim: 0,
        vestingDuration: 0,
        vestingPeriodicity: 0,
        totalFunders,
        rounds: roundData,
        saleRaised: BigNumber.from(0),
      };
      setState((prev) => ({
          ...prev,
          loading: false,
         data: {
           ...ido,
           meta: metadata,
         },
      }));

    } else {
    try {
      const result = (await fetchQuery(query, { id }, idoGraphHttpEndPoint))
        .data;
      if (result.data && result.data.ido) {
        const isSec = result.data.ido.version === "sec";

        const calls = [
          "owner",
          "startTime",
          "endTime",
          "claimTime",
          "saleToken",
          "saleTarget",
          "fundToken",
          "fundTarget",
          "fundRaised",
          "totalReleased",
          isSec ? "fcfsAmount" : "baseAmount",
          "minFundAmount",
          "meta",
          "cliffTime",
          "distributePercentAtClaim",
          "vestingDuration",
          "vestingPeriodicity",
          "getFundersCount",
        ].map((method) => ({
          name: method,
          address: id,
          params: [],
        }));
        const [
          [creator],
          [startTime],
          [endTime],
          [claimTime],
          [saleToken],
          [saleTarget],
          [fundToken],
          [fundTarget],
          [fundRaised],
          [totalReleased],
          [baseAmount],
          [minFundAmount],
          [meta],
          [cliffTime],
          [distributePercentAtClaim],
          [vestingDuration],
          [vestingPeriodicity],
          [totalFunders],
        ] = await multicall.multicallv2(isSec ? IDOSecAbi : IDOv4Abi, calls, {
          requireSuccess: false,
        });

        let roundData: IRoundData[] = [];

        if (isSec) {
          const roundCalls = [1, 2].map((roundNumber) => ({
            name: "roundsFundRaised",
            address: id,
            params: [roundNumber],
          }));
          const roundResponse = await multicall.multicallv2(
            IDOv4Abi,
            roundCalls,
            {
              requireSuccess: false,
            }
          );

          roundData = roundResponse.map((info: BigNumber[], index: number) =>
            info && info.length > 0
              ? ({
                  roundId: BigNumber.from(index + 1),
                  fundRaised: info[0],
                  saleRaised: info[0].mul(saleTarget).div(fundTarget),
                } as IRoundData)
              : null
          );
        } else {
          const roundCalls = [1, 2, 3, 4, 5, 6].map((roundNumber) => ({
            name: "roundsFundRaised",
            address: id,
            params: [roundNumber],
          }));
          const roundResponse = await multicall.multicallv2(
            IDOv4Abi,
            roundCalls,
            {
              requireSuccess: false,
            }
          );

          roundData = roundResponse.map((info: BigNumber[], index: number) =>
            info && info.length > 0
              ? ({
                  roundId: BigNumber.from(index + 1),
                  fundRaised: info[0],
                  saleRaised: info[0].mul(saleTarget).div(fundTarget),
                } as IRoundData)
              : null
          );
        }

        const ido = {
          ...result.data.ido,
          address: id,
          creator,
          startTime: startTime.toNumber(),
          endTime: endTime.toNumber(),
          claimTime: claimTime.toNumber(),
          saleToken,
          saleTarget,
          fundToken,
          fundTarget,
          fundRaised,
          totalReleased,
          baseAmount,
          minFundAmount,
          meta,
          cliffTime: cliffTime.toNumber(),
          distributePercentAtClaim: distributePercentAtClaim.toNumber(),
          vestingDuration: vestingDuration.toNumber(),
          vestingPeriodicity: vestingPeriodicity.toNumber(),
          totalFunders,
          rounds: roundData,
          saleRaised: fundRaised.mul(saleTarget).div(fundTarget),
        };
        try {
          const metadata = (await axios.get(ido.meta)).data;
          setState((prev) => ({
            ...prev,
            loading: false,
            data: {
              ...ido,
              meta: metadata,
            },
          }));
        } catch (error) {
          setState((prev) => ({
            ...prev,
            loading: false,
            data: {
              ...ido,
              meta: undefined,
            },
          }));
        }
      } else {
        setState(() => ({ loading: false }));
      }
    } catch (error) {
      console.error(error);
      setState(() => ({ loading: false }));
    }
    }
  };

  useEffect(() => {
    loadIDO();

    let interval = setInterval(loadIDO, 60000);

    return () => {
      clearInterval(interval);
    };
  }, [idoGraphHttpEndPoint, id]);

  return { ...state, load: loadIDO };
};
