interface IProps {
  className?: string;
  onClose: () => void;
}

interface IState {
  tierId: number;
  point: BigNumber;

  stakedPlayAmount: BigNumber;
  stakedPlayLongAmount: BigNumber;
  lockedPlayBusdAmount: BigNumber;
}

const defaultState: IState = {
  tierId: 0,
  point: ZERO,
  stakedPlayAmount: ZERO,
  stakedPlayLongAmount: ZERO,
  lockedPlayBusdAmount: ZERO,
};

export const TIERS = [
  {
    id: 4,
    name: "Platinum",
    stake: 2000,
    multiplier: 20,
    icon: "/assets/tiers/Platinum.png",
  },
  {
    id: 3,
    name: "Gold",
    stake: 1000,
    multiplier: 10,
    icon: "/assets/tiers/Gold.png",
  },
  {
    id: 2,
    name: "Silver",
    stake: 400,
    multiplier: 4,
    icon: "/assets/tiers/Silver.png",
  },

  {
    id: 1,
    name: "Bronze",
    stake: 100,
    multiplier: 1,
    icon: "/assets/tiers/Bronze.png",
  },
];

export const TierModal = (props: IProps) => {
  const classes = useStyles();
  const { onClose } = props;
  const { account, library: provider, networkId } = useConnectedWeb3Context();

  const [state, setState] = useState<IState>(defaultState);

  useEffect(() => {
    let isMounted = true;

    const loadInfo = async () => {
      if (!account || !provider) {
        setState((prev) => ({ ...prev, ...defaultState }));
        return;
      }

      try {
        const tierFactor = getTierFactor(networkId);

        const tier = new TierService(provider, account, tierFactor.tier);
        const pointService = new PointService(
          provider,
          account,
          tierFactor.point
        );
        const playStaking = new StakingService(
          provider,
          account,
          tierFactor.staking
        );
        const playStakingLong = new StakingService(
          provider,
          account,
          tierFactor.stakingLong
        );
        const playBusdLocking = new LpMiningService(
          provider,
          account,
          tierFactor.mplayBusdLock
        );

        const [
          stakedPlayAmount,
          stakedPlayLongAmount,
          tierId,
          point,
          lockedPlayBusdAmount,
        ] = await Promise.all([
          playStaking.getDepositedAmount(account),
          playStakingLong.getDepositedAmount(account),
          tier.getTierId(account),
          pointService.getPoint(account),
          playBusdLocking.getDepositedAmount(account),
        ]);
        if (isMounted)
          setState((prev) => ({
            ...prev,
            stakedPlayAmount,
            stakedPlayLongAmount,
            tierId,
            point,
            lockedPlayBusdAmount,
          }));
      } catch (error) {
        console.error(error);
        if (isMounted) setState((prev) => ({ ...prev, ...defaultState }));
      }
    };

    loadInfo();

    return () => {
      isMounted = false;
    };
  }, [account, networkId]);

  return (
    <Modal onClose={onClose} open>
      <div className={clsx(classes.root, props.className)}>
        <Typography className={classes.title}>Tier Info</Typography>
        <div className={classes.pointTableWrapper}>
          <table className={classes.pointTable}>
            <thead>
              <tr>
                <th>Balance</th>
                <th>Amount</th>
                <th>Multiplier</th>
                <th>MPlay Quantity</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>Staked MPlay(30D)</td>
                <td>
                  {formatBigNumber(state.stakedPlayAmount, ETHER_DECIMAL)}
                </td>
                <td>1.5</td>
                <td>
                  {formatBigNumber(
                    state.stakedPlayAmount.mul(15).div(10),
                    ETHER_DECIMAL
                  )}
                </td>
              </tr>
              <tr>
                <td>Staked MPlay(90D)</td>
                <td>
                  {formatBigNumber(state.stakedPlayLongAmount, ETHER_DECIMAL)}
                </td>
                <td>2.2</td>
                <td>
                  {formatBigNumber(
                    state.stakedPlayLongAmount.mul(22).div(10),
                    ETHER_DECIMAL
                  )}
                </td>
              </tr>

              <tr>
                <td>Locked MPlay-Busd</td>
                <td>
                  {formatBigNumber(state.lockedPlayBusdAmount, ETHER_DECIMAL)}
                </td>
                <td>4</td>
                <td>
                  {formatBigNumber(
                    state.lockedPlayBusdAmount.mul(4),
                    ETHER_DECIMAL
                  )}
                </td>
              </tr>
              <tr>
                <td>Your Point</td>
                <td></td>
                <td></td>
                <td>{formatBigNumber(state.point, ETHER_DECIMAL, 2)}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div className={classes.tiersWrapper}>
          <Grid container spacing={2}>
            {TIERS.map((tier) => (
              <Grid item xs={6} sm={3} key={`${tier.id}`}>
                <div
                  className={clsx(
                    classes.tierItem,
                    tier.id === state.tierId ? "active" : ""
                  )}
                >
                  <img
                    alt="icon"
                    className={classes.tierIcon}
                    src={tier.icon}
                  />
                  <Typography align="center" className={classes.tierItemTitle}>
                    {tier.name}
                  </Typography>
                  <Typography
                    align="center"
                    className={classes.tierItemCondition}
                  >
                    {tier.stake} Points
                  </Typography>
                  <Typography align="center" className={classes.tierItemMulti}>
                    <span>{tier.multiplier}x</span> multiplier allocation*
                  </Typography>
                  {tier.id === state.tierId && (
                    <Typography align="center" className={classes.yourTier}>
                      Your Tier
                    </Typography>
                  )}
                </div>
              </Grid>
            ))}
          </Grid>
        </div>
      </div>
    </Modal>
  );
};
