import axios from "axios";
import {
  DefaultReadonlyProvider,
  getToken,
  getTokenFromAddress,
  networkIds,
} from "config/networks";
import { useConnectedWeb3Context } from "contexts";
import { BigNumber } from "ethers";
import { parseEther } from "ethers/lib/utils";
import { useEffect, useState } from "react";
import { ERC20Service } from "services/erc20";
import { ONE_ETHER, ZERO } from "utils/number";

interface IState {
  token0: BigNumber;
  token1: BigNumber;
}

export const useTokenPrice = (address: string) => {
  const [state, setState] = useState<IState>({
    token0: ZERO,
    token1: ZERO,
  });
  const { library: provider, networkId } = useConnectedWeb3Context();

  useEffect(() => {
    const calculatePrice = async () => {
      if (networkId === networkIds.HARMONY) {
        if (!provider) {
          return;
        }
        const token = getTokenFromAddress(address, networkId);

        if (token.symbol === "onep") {
          const lpToken = getToken("onep-one", networkId);
          const woneToken = getToken("wone", networkId);
          const wone = new ERC20Service(provider, "", woneToken.address);
          const token0 = new ERC20Service(provider, "", address);
          try {
            const response = (
              await axios.get(
                `https://api.coingecko.com/api/v3/simple/price?ids=harmony&vs_currencies=usd`
              )
            ).data;
            const wonePrice = parseEther(String(response.harmony.usd));

            const [woneBalance, token0Balance] = await Promise.all([
              wone.getBalanceOf(lpToken.address),
              token0.getBalanceOf(lpToken.address),
            ]);

            const token0Price = woneBalance.mul(wonePrice).div(token0Balance);
            setState(() => ({
              token0: token0Price,
              token1: wonePrice,
            }));
          } catch (error) {
            setState(() => ({
              token0: ZERO,
              token1: ZERO,
            }));
          }
        }
      } else {
        const token = getTokenFromAddress(address, networkIds.BSC);

        const finalLPTokenSymbol = (() => {
          switch (token.symbol.toLowerCase()) {
            case "mplay":
              return "mplay-busd";
            case "onep":
              return "onep-busd";
            case "gotem":
              return "gotem-busd";
            case "qdrop":
              return "qdrop-busd";
            case "pee":
              return "pee-busd";
            case "hbarp":
              return "hbarpad-busd";
            case "elit":
              return "elit-busd";
            case "legio":
              return "mplay-busd"; // TODO:  legio-busd
            default:
              return "mplay-busd";
          }
        })();

        const lpToken = getToken(finalLPTokenSymbol, networkIds.BSC);
        const busdToken = getToken("busd", networkIds.BSC);
        const busd = new ERC20Service(
          DefaultReadonlyProvider,
          "",
          busdToken.address
        );
        const token0 = new ERC20Service(
          DefaultReadonlyProvider,
          "",
          lpToken.token0 || ""
        );
        try {
          const [busdBalance, token0Balance] = await Promise.all([
            busd.getBalanceOf(lpToken.address),
            token0.getBalanceOf(lpToken.address),
          ]);

          const token0Price = busdBalance.mul(ONE_ETHER).div(token0Balance);
          setState(() => ({
            token0: token0Price,
            token1: ONE_ETHER,
          }));
        } catch (error) {
          setState(() => ({
            token0: ZERO,
            token1: ZERO,
          }));
        }
      }
    };

    calculatePrice();

    let interval = setInterval(calculatePrice, 30000);

    return () => {
      clearInterval(interval);
    };
  }, [address]);

  return state;
};
