import { DefaultReadonlyProvider, getToken, networkIds } from "config/networks";
import { BigNumber } from "ethers";
import { useEffect, useState } from "react";
import { ONE_ETHER, ZERO } from "utils/number";
import axios from "axios";
import { parseEther } from "ethers/lib/utils";
import { ERC20Service } from "services/erc20";

interface IState {
  price: BigNumber;
}

export const usePricePlay = () => {
  const [state, setState] = useState<IState>({ price: ZERO });

  const mplay = getToken("mplay", networkIds.BSC);
  const busd = getToken("busd", networkIds.BSC);
  const mplayBusd = getToken("mplay-busd", networkIds.BSC);

  useEffect(() => {
    let isMounted = true;

    const loadPlayPrice = async () => {
      try {
        const playToken = new ERC20Service(
          DefaultReadonlyProvider,
          "",
          mplay.address
        );
        const busdToken = new ERC20Service(
          DefaultReadonlyProvider,
          "",
          busd.address
        );

        const [playBalance, busdBalance] = await Promise.all([
          playToken.getBalanceOf(mplayBusd.address),
          busdToken.getBalanceOf(mplayBusd.address),
        ]);

        setState((prev) => ({
          ...prev,
          price: busdBalance.mul(ONE_ETHER).div(playBalance),
        }));
      } catch (error) {
        console.error(error);
        setState((prev) => ({ ...prev, price: ZERO }));
      }
    };

    loadPlayPrice();

    let interval = setInterval(loadPlayPrice, 60000);

    return () => {
      isMounted = false;
      clearInterval(interval);
    };
  }, []);

  return state.price;
};
