import { getGraphInfo, networkIds } from "config/networks";
import { useConnectedWeb3Context } from "contexts";
import { useEffect, useState } from "react";
import { IIDO } from "types";
import { getCurrentTimeStamp } from "utils/ido";
import { fetchQuery } from "utils/thegraph";
import { IDOS_TO_HIDE } from "config/constants";

const query = `
    query($banIds: [ID!],$first: Int!, $skip: Int!, $timestamp: String!) {
        idos(first: $first, skip: $skip, orderBy: claimTime, orderDirection: asc, where: {id_not_in:$banIds,claimTime_lte: $timestamp,fundRaised_gt: 0, cliffTime_gt: 0}) {
            id
            address
        }
    }
`;

const wrangeIDO = (ido: any) =>
  ({
    ...ido,
  } as IIDO);

interface IState {
  loading: boolean;
  data: IIDO[];
  hasMore: boolean;
}

const PerPage = 15;

export const useClaimableIDOs = () => {
  const { networkId } = useConnectedWeb3Context();
  const idoGraphHttpEndPoint = getGraphInfo("ido", networkId).httpUri;
  const [state, setState] = useState<IState>({
    loading: true,
    data: [],
    hasMore: false,
  });

  const loadIDOs = async (skip: number) => {
    if (networkId === networkIds.BSCTEST) {
      setState((prev) => ({ ...prev, loading: false }));
      return;
    }
    setState((prev) => ({ ...prev, loading: true }));
    try {
      const currentTimeStamp = getCurrentTimeStamp();
      const result = (
        await fetchQuery(
          query,
          {
            banIds: IDOS_TO_HIDE,
            skip,
            first: PerPage + 1,
            timestamp: currentTimeStamp.toString(),
          },
          idoGraphHttpEndPoint
        )
      ).data;
      // add legio claim      
      let legio_claim_ido = {'id':'0xcEade12851e9Dcc530ED2314E5E4677645e9b3e7', 'address':'0xcEade12851e9Dcc530ED2314E5E4677645e9b3e7'} // TODO: 
      result.data.idos.unshift(legio_claim_ido) // add at beginning
      if (result.data && result.data.idos) {
        setState((prev) => ({
          ...prev,
          loading: false,
          data:
            skip === 0
              ? result.data.idos
                  .map((ido: any) => wrangeIDO(ido))
                  .slice(0, PerPage)
              : [
                  ...state.data,
                  ...result.data.idos
                    .map((ido: any) => wrangeIDO(ido))
                    .slice(0, PerPage),
                ],
          hasMore: result.data.idos.length > PerPage,
        }));
      } else {
        setState((prev) => ({ ...prev, loading: false, data: [] }));
      }
    } catch (error) {
      setState((prev) => ({ ...prev, loading: false, data: [] }));
    }
  };

  const loadMore = async () => {
    await loadIDOs(state.data.length);
  };

  useEffect(() => {
    setState((prev) => ({ ...prev, data: [], hasMore: false }));
    loadIDOs(0);
  }, [networkId]);

  return { ...state, load: loadIDOs, loadMore };
};
